.math-question {
	margin: 2em 3vw;
	padding: 2em;
	background: white;
	box-shadow: 0 0 1.5em #546e7a;
	border-radius: 2em;
}

.katex {
	overflow: auto;
}

.math-q .katex {
	padding: 0 !important;
	height: fit-content;
}

.math-a .katex {
	display: unset !important;
}

.col {
	border: 0.15em solid transparent;
	color: #455a64;
	background: #eceff1;
	border-radius: 2em;
	margin: 0.5em;
	cursor: pointer;
	transition: 250ms;
}

.locked {
	cursor: auto;
}

.selected {
	border: 0.15em solid #3f51b5;
	color: #303f9f;
	background: #c5cae9;
}

.selected-correct {
	border: 0.15em solid transparent;
	color: white;
	background: #00c853;
}

.unselected-correct {
	border: 0.15em solid transparent;
	color: white;
	background: #d50000;
}

@media (hover: hover) {
	.math-q .row {
		pointer-events: none;
	}

	.math-q ::-webkit-scrollbar {
		display: none;
	}

	.col:not(.selected):not(.locked):hover {
		color: #827717;
		background: #fff9c4;
		box-shadow: 0 0 1em rgba(130, 119, 23, 0.5);
	}
}
